<template>
  <div>
    <van-card
      :title="userInfo.nickname"
      :desc="'邮箱号：  ' + userInfo.email"
      centered
    >
      <div slot="thumb">
        <img
          size="mini"
          :src="userInfo.picture"
          alt=""
          style="
            width: 50%;
            height: 50%;
            border-radius: 50%;
            float: left;
            margin-left: 40px;
            margin-top: 30px;
          "
        />
      </div>
    </van-card>
    <van-list>
      <van-cell
        class="van_cell"
        :icon="item.icon"
        v-for="item in userSetList"
        :key="item.id"
        :title="item.title"
      >
        <van-icon name="arrow" @click="goIn(item.path)"></van-icon>
      </van-cell>
      <van-cell class="van_cell" title="退出登录" icon="share">
        <van-icon name="arrow" @click="loginOut"></van-icon>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { userInfoAPI } from '@/api/api'
export default {
  name: 'userIndex',
  data () {
    return {
      userInfo: {},
      // userSetList: ['个人信息', '账号安全', '设置', '帮助与反馈', '关于', '退出登录']
      userSetList: [
        { title: '个人信息', icon: 'manager', path: '/userInfo' },
        { title: '账号安全', icon: 'youzan-shield', path: '/userSafe' },
        { title: '设置', icon: 'setting', path: '/userSetting' },
        { title: '帮助与反馈', icon: 'comment', path: '/userHelp' },
        { title: '关于', icon: 'info', path: '/userAbout' }
      ]
    }
  },
  created () {
    this.getUserInfo()
  },
  methods: {
    getUserInfo () {
      userInfoAPI().then((res) => {
        this.userInfo = res.data
        console.log(this.userInfo)
      })
    },
    goIn (path) {
      this.$router.push(path)
    },
    loginOut () {
      localStorage.removeItem('user_token')
      console.log('退出登录成功')
      this.$router.push('/login')
    }
  }
}
</script>

<style>
.van-card {
  background: url(https://ts1.cn.mm.bing.net/th/id/R-C.7ced2944c885ed676e461ac629d2f0f5?rik=aVxQZZUxejPK1g&riu=http%3a%2f%2fpic18.nipic.com%2f20120115%2f9144829_170018769112_2.jpg&ehk=gaPAa4ivwZegwPahSEuorVSFJYEa5qTbvigBXzfRzgw%3d&risl=&pid=ImgRaw&r=0);
}
.van-card .van-card__thumb {
  border-radius: 50%;
}

</style>
